.otp-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 16px;
    height: 20px;
    margin-top: 128px;
    margin-bottom: 32px;
}

.otp-input {
    height: 20px;
    width: 20px;
    border-radius: 100%;
    border: 1px solid var(--grey-200);
}


.otp-input-filled {
    height: 20px;
    width: 20px;
    border-radius: 100%;
    border: none;
    background: var( --primary-gradient);
    transition: background-color 0.5s ease-in-out, border-color 0.5s ease-in-out;
}
